body {
  color: #1D2124;
  font-family: "Hiragino Kaku Gothic ProN";
  font-size: 1rem;
  font-weight: 300;
  line-height: 2.25;
}

.c-accordion {
  border: 0.125rem solid #666;
}

.c-accordion summary::-webkit-details-marker {
  display: none;
}

.c-accordion__title {
  padding: 1.25rem 2.5rem;
  font-size: 1.125rem;
  background: #666;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.c-accordion__icon i {
  transition: transform 0.3s;
  right: 0;
  position: relative;
  font-size: 1rem;
}

.c-accordion.is-open .c-accordion__icon i {
  transform: rotate(-180deg);
}

.c-accordion__content {
  font-size: 1.125rem;
  background: #fff;
  color: #666;
  text-decoration: none;
  overflow: hidden;
}

.c-accordion__content-inner {
  padding: 1.25rem 2.5rem;
}

/* .c-accordion--type1 */
.c-accordion--type1 .c-accordion__title {
  background: #fff;
  color: #666;
  transition: border 0.3s;
}

.c-accordion--type1 .c-accordion__icon i {
  font-size: 1.25rem;
}

/* .c-accordion--type2 */
.c-accordion--type2 .c-accordion__title {
  position: relative;
}

.c-accordion--type2 .c-accordion__icon::before,
.c-accordion--type2 .c-accordion__icon::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.875rem;
  width: 0.9375rem;
  height: 0.125rem;
  background: #fff;
  transform: translate(-50%, -50%);
  transition: 0.3s all;
}

.c-accordion--type2 .c-accordion__icon::after {
  transform: translate(-50%, -50%) rotate(-90deg);
}

.c-accordion--type2.is-open .c-accordion__icon::after {
  transform: translate(-50%, -50%) rotate(0deg);
}

.c-button {
  display: inline-block;
  text-align: center;
  transition: 0.3s;
  padding: 1.25rem 2.5rem;
  max-width: 100%;
  font-size: 1.125rem;
  background: #666;
  color: #fff;
  border: 0.0625rem solid #666;
  text-decoration: none;
}

.c-button:hover {
  background: #fff;
  color: #666;
}

.c-button--arrow {
  position: relative;
  padding-right: 3.125rem;
}

.c-button--arrow::after {
  content: "\f105";
  position: absolute;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  top: 50%;
  right: 0.9375rem;
  transform: translateY(-50%);
  font-size: 1rem;
}

.c-card__body {
  padding: 0.9375rem;
}

.c-card__time {
  font-size: 0.875rem;
}

.c-card__title {
  font-weight: bold;
  font-size: 1.25rem;
  margin-top: 0.625rem;
}

.c-card__text {
  margin-top: 0.625rem;
}

.c-card--shadow {
  box-shadow: 0 0 0.3125rem 0 rgba(0, 0, 0, 0.3);
}

.c-card--horizon {
  display: grid;
  grid-template-columns: 20% 80%;
}

.c-card--horizon .c-card__body {
  padding: 0.625rem 0.9375rem;
}

.c-drawer__button {
  display: flex;
  height: 3.125rem;
  width: 3.125rem;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 2rem transparent;
  position: relative;
  z-index: 200;
  background: #ccc;
}

.c-drawer__button:hover,
.c-drawer__button:focus {
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
  background: #666;
}

.c-drawer__line {
  font-size: 0;
}

.c-drawer__line,
.c-drawer__line::before,
.c-drawer__line::after {
  content: "";
  display: block;
  height: 0.1875rem;
  width: 1.5625rem;
  border-radius: 0.1875rem;
  background-color: #fff;
  position: absolute;
  transition: all 0.2s ease-in-out;
}

.c-drawer__line::before {
  bottom: 0.5rem;
}

.c-drawer__line::after {
  top: 0.5rem;
}

.c-drawer__button[aria-expanded=true] {
  background: #666;
}

.c-drawer__button[aria-expanded=true] .c-drawer__line {
  background-color: transparent;
}

.c-drawer__button[aria-expanded=true] .c-drawer__line::before {
  bottom: 0;
  transform: rotate(45deg);
}

.c-drawer__button[aria-expanded=true] .c-drawer__line::after {
  top: 0;
  transform: rotate(-45deg);
}

.c-drawer__modal {
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s;
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(255, 255, 255, 0.95);
  display: block;
}

.c-drawer__modal.is-open {
  visibility: visible;
  opacity: 1;
  z-index: 100;
}

.c-drawer__lists {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.c-drawer__link {
  font-size: 1.25rem;
  font-weight: bold;
  display: block;
  color: #666;
  padding-block: 1.25rem;
  text-align: center;
}

/* c-drawer--open-right */
.c-drawer--open-right .c-drawer__modal {
  transform: translateX(100%);
}

.c-drawer--open-right .c-drawer__modal.is-open {
  transform: translateX(0);
}

/* c-drawer--open-left */
.c-drawer--open-left .c-drawer__modal {
  transform: translateX(-100%);
}

.c-drawer--open-left .c-drawer__modal.is-open {
  transform: translateX(0);
}

/* c-drawer--open-top */
.c-drawer--open-top .c-drawer__modal {
  transform: translateY(-100%);
}

.c-drawer--open-top .c-drawer__modal.is-open {
  transform: translateY(0);
}

